.weather-container {
  background: #ffa500;
  box-shadow: 2px 4px 16px 0 rgba(0, 0, 0, 0.5);
  height: 625px;
  width: 80%;
  margin: auto;
  border-radius: 5px;
  position: relative;
  padding: 10px;
}

.weather-header h3 {
  font-size: 1.4rem;
  font-weight: 500;
  letter-spacing: 2px;
  text-align: center;
}

.weather-header div {
  display: flex;
  border-bottom: 2px solid #eee;
  width: 80%;
  margin: auto;
}

.weather-header button {
  border: none;
  background: transparent;
  color: #17a589;
  cursor: pointer;
  outline: none;
}

.search-engine {
  background: transparent;
  border: none;
  padding: 4px;
  outline: none;
  width: 100%;
  color: #d5d8dc;
  font-size: 1.2rem;
}

.search-engine::-webkit-input-placeholder {
  font-size: 1.1rem;
  color: #eee;
  opacity: 0.5;
}

.weather-info {
  display: flex;
  flex-direction: column;
  height: 300px;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  margin-top: 100px;
  text-align: center;
}

.weather-info i {
  color: yellow;
  position: relative;
  top: 5px;
  left: 5px;
  animation: rotateSun 5s infinite linear;
}

@keyframes rotateSun {
  to {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  margin-top: 30px;
  margin-bottom: 0;
  font-size: 1.3rem;
  font-weight: bold;
}

.degree-symbol::after {
  content: "\00b0";
}

/*For Mobile Responsiveness*/
@media screen and (mid-wdith: 700px) {
  .weather-container {
    height: 400px;
    width: 70%;
  }
  .weather-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 25px;
  }
  weather-header h3 {
    margin: 0;
  }
  .weather-header div {
    width: 50%;
    margin: 0;
  }
  .weather-info {
    display: flex;
    flex-direction: row;
  }
}
